/**
*   type==1  type==2 type==3
*/
GameApi.prototype.upUserPetLevel = function (pid, type, point) {
    let parms = { pid: pid, type };
    if (type == 2) {
        point = point || {};
        Object.assign(parms, {
            str: point.str || null,
            int: point.int || null,
            agi: point.agi || null,
            vit: point.vit || null,
            con: point.con || null
        });
    }
    this.sendMessage(parms, "connector.userHandler.upUserPetLevel");
}

